@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
:root{
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
  background-image: url(/portfolio/static/media/background.723d0e41.png);
}
.App
{
  width: 100vw;
  min-height: 100vh;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center;
}

.home
{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.skills {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.skillList {
  margin: 1rem;
  font-style: italic;
}

h1 {
  margin-top:1rem;
  margin-bottom:1rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

h2 {
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

h3 {
  margin-top:.2rem;
  margin-bottom:.2rem;
  text-decoration: underline;
}

h4 {
  font-size: small;
  font-style: italic;
}


.compdet {
  padding: 1rem;
}

.profilepic
{
  border-radius: 50%;
  width: 12rem;
  margin: 30px;
}
.typingeffect
{
  font-size: 3rem;
  font-size: var(--typing-font);
}


a
{
  text-decoration: none;
  color: black;
}

a:hover {
  font-size: large;
}



.widecard
{
  border:0px solid #9a9b9c;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  margin-top: .3rem;
  margin-bottom: .3rem;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}




